<template>
 <div class="CultrueItems">
    <!-- <div class="nav"><span class="topWord">浦东文明创建单位单位展示</span></div> -->
    <div class="list">
        <div v-for="item,i in buttonlist" :key="i" class="item" @mouseover="over(item,$event)" @mouseleave="leave(item,$event)" @click="gohtml">
        <img class="img" :src="item.url" alt="无资源">
        <div class="word">
           {{item.name}}
        </div >
        </div>
    </div>
 </div>
</template>

<script>
 export default {
    name:"CultrueItems",
   data () {
    let buttonlist=[
        {
            name:'文明单位创建',
            url:require("@/assets/img/house.png"),
            hurl:require("@/assets/img/houseW.png")
        },
        {
            name:'文明社区创建',
            url:require("@/assets/img/build.png"),
             hurl:require("@/assets/img/buildW.png")
        },
        {
            name:'文明校园创建',
            url:require("@/assets/img/school.png"),
             hurl:require("@/assets/img/schoolW.png")
        },
        {
            name:'文明小区创建',
            url:require("@/assets/img/sun.png"),
             hurl:require("@/assets/img/sunW.png")
        },
    ]
     return {
        buttonlist
     }
   },
   components: {

   },
   methods:{
    over(item,e){
        e.target.children[0].src=item.hurl;
    },
    leave(item,e){
          e.target.children[0].src=item.url;
    },
    gohtml(){
      this.$router.push('DetailList')
    }

   }
 }
</script>

<style scoped>
.nav{
    height: 50px;
    margin: 0 33px 0 33px;
    text-align: center;
    line-height: 50px;

}
 .topWord{
        font-size: 1rem;
    color:black;
    font-weight: 600;
 }
 .list{
    width: 1170px;
    margin: 0 auto;
    display: flex;
    justify-content: space-around;
 }
 .item{
    position: relative;
   cursor: pointer;
    width: 262px;
    height: 288px;
    border:1px solid #dcdcdc; 
 }
 .img{
   pointer-events: none;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
 }
  .item:hover{
    transition:all 0.7s;
    background-color: #ED9609;
    color: white;
 }
 .word{
   pointer-events: none;

    position: absolute;
    bottom: 20px;
    text-align: center;
    width: 100%;
 }
</style>
